.menu-bar {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;

  .navBar-wrapper {
    display: grid;
    padding-top: 1em;
    height: 50px;
    .navBar {

      background-color: rgb(248, 248, 248);
      box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
      display: grid;
      grid-auto-flow: column;
      width: 100%;
      column-gap: 0;
      border-radius: 10px;

      .subNav {
        cursor: pointer;
        width: 100%;
        position: relative;

        &:not(:first-child) {
          border-left: 1px solid rgba(254, 207, 0, 1);
        }

        &:hover {

          .subNavBtn {
            display: grid;
            cursor: pointer;
            animation: unfadingYellow 0.5s forwards;
          }

          .dropdown {
            display: grid;
            cursor: pointer;

          }
        }

        .subNavBtn {
          font-size: 16px;
          border: none;
          outline: none;
          padding: 14px 16px;
          background-color: inherit;
          font-family: inherit;
          margin: 0;
          width: 100%;
        }

        .dropdown {
          display: none;
          position: absolute;
          background-color: rgb(236, 236, 236);
          width: 100%;
          z-index: 1;
          border-radius: 10px;

          .subNavBtn {
            &:hover {
              animation: unfadingWhite 0.5s forwards;
              border: 1px solid rgba(254, 207, 0, 1);
            }
          }
        }

      }
    }
  }
}

@keyframes unfadingYellow {
  0% {
    background-color: rgba(254, 207, 0, 0.1);
  }
  50% {
    background-color: rgba(254, 207, 0, 0.6);
  }
  100% {
    background-color: rgba(254, 207, 0, 1);
  }
}

@keyframes unfadingWhite {
  0% {
    background-color: rgba(236, 236, 236, 0.1);
  }
  50% {
    background-color: rgba(236, 236, 236, 0.6);
  }
  100% {
    background-color: rgba(236, 236, 236, 1);
  }
}

@media only screen and (max-width: 1024px) {
  .menu-bar {
    display: grid;
    grid-auto-flow: row;
    grid-template-columns: unset;

  }
}